export default {
	data() {
		return {
			iswait:false,
			id:''
		}
	},
	onLoad(options){
		this.id = options
	},
	methods: {
		
		canceling(){
			uni.navigateTo({
			    url: '../canceloriginal/canceloriginal?id='+this.id
			});
		},
		wait(){
			uni.navigateBack({
			    url: '../waitreview/waitreview',
				animationType: 'none',
				animationDuration: 200
			});
		},
	}
}
